<template>
	<view class="" style="padding: 0 30rpx;">
		<view class="title">
			<text>{{orderDetail.paymentStatus}}</text>
			<view class="paycount">
				<text class="count">{{orderDetail.payAmount}}</text>
				<text>元</text>
				<!-- <view class="" style="color: #FF4E56;">
					<text>已逾期：</text>
					<text>1小时29分钟</text>
				</view> -->
			</view>
		</view>
		<view class="" style="font-size: 30rpx;color: #000000;padding: 30rpx 0;">
			<text>订单信息</text>
		</view>
		<view class="" style="background-color: #FFFFFF;padding: 30rpx 28rpx;border-radius: 15rpx;">
			<view class="item">
				<text class="time right" style="">交易对象</text>
				<text>{{orderDetail.targetName}}</text>
			</view>
			<view class="item">
				<text class="time right" style="">订单内容</text>
				<text>{{orderDetail.orderSubject}}</text>
			</view>
			<view class="item">
				<u-collapse :head-style='headStyle' style='width: 100%;'>
					<u-collapse-item v-for="(item,index) in orderDetail.omsOrderItems" :key='index'>
						<view class="" slot="title">
							<text style="margin-right: 38rpx;width: 112rpx;font-size: 28rpx;">订单详情</text>
							<text style="color: #000000;">树蚁辅助设计</text>
						</view>
						<view style="display: flex;">
							<text style="margin-right: 38rpx;width: 112rpx;display: block;">协助内容</text>
							<view class="" style="color: #000000;flex: 1;" v-if="item.assistExtJson">
								<view class="">
									<text v-if="JSON.parse(item.assistExtJson).productType==3">手术方案设计(包含：1:三维重建，2:模型观察、分析、测量)</text>
									<text v-if="JSON.parse(item.assistExtJson).productType==2">模型观察、分析、测量</text>
									<text v-if="JSON.parse(item.assistExtJson).productType==1">三维重建</text>
								</view>
								 <view class="">
								 	<text v-if="JSON.parse(item.assistExtJson).softTissueBoolean">重建软组织</text>
								 </view>
								<view class="">
									<text v-if="JSON.parse(item.assistExtJson).guidePlateDesignBoolean">导板设计</text>
								</view>
								<view class="">
									<text v-if="JSON.parse(item.assistExtJson).instrumentDesignBoolean">器械设计</text>
								</view>
							</view>
						</view>
					</u-collapse-item>
				</u-collapse>
			</view>
			<view class="item">
				<text class="time right" style="">创建时间</text>
				<text>{{orderDetail.createTime|time}}</text>
			</view>
			<view class="item">
				<text class="time right" style="">订单编号</text>
				<text>{{orderDetail.orderNo}}</text>
			</view>
		</view>
		<view class="" style="font-size: 30rpx;color: #000000;padding: 30rpx 0;">
			<text>账单信息</text>
		</view>
		<view class="" v-for="(item,index) in billList" :key='index' style="background-color: #FFFFFF;padding: 30rpx 28rpx;border-radius: 15rpx;margin-bottom: 30rpx;">
			<view class="item" style="display: flex;justify-content: space-between;">
				<view class="" style="display: flex;">
					<text class="time right" style="">金额</text>
					<text>{{item.totalAmount}}</text>
				</view>
				<u-tag v-if="item.billStatus=='WFK'" @click='payment(item)' text='付款' shape="circle" bg-color='#ffffff' color='#86B0D4' border-color='#86B0D4'/>
				<u-tag v-if="item.billStatus=='YFK'" text='已付款' shape="circle" bg-color='#ffffff' color='#666666' border-color='#666666'/>
				<u-tag v-if="item.billStatus=='YQ'" @click='payment(item)' text='逾期' shape="circle" bg-color='#ffffff' color='#FF4E56' border-color='#FF4E56'/>
			</view>
			<view class="item">
				<text class="time right" style="">创建时间</text>
				<text>{{item.createTime|time}}</text>
			</view>
			<view class="item" v-if="item.paymentTime">
				<text class="time right" style="">付款时间</text>
				<text>{{item.paymentTime|time}}</text>
			</view>
			<view class="item">
				<text class="time right" style="">账单内容</text>
				<text>{{item.billSubject}}</text>
			</view>
			<view class="item">
				<text class="time right" style="">账单编号</text>
				<text>{{item.billNo}}</text>
			</view>
		</view>
		<view class="" style="height: 110rpx;">
			
		</view>
		<view class="footer">
			<view class="vrshow" style="">
				<view class="" v-for="(item,index) in boottomList" :key='index' @click="show(index,orderDetail.orderNo)">
					<view class="" style="margin-bottom: 10rpx;">
						<!-- <text class="iconfont" :class="item.icon"></text> -->
						<u-icon :name="item.icon" color="#232323" size="42"></u-icon>
					</view>
					<text>
						{{item.title}}
					</text>
				</view>
			</view>
		</view>
	</view>
</template>

<script>
	import {getOrderList,getOrderDetail,paymentResult,getOpenid} from '@/utill/api/order/order.js'
	import {time} from '@/utill/tools/timestamp.js'
	export default{
		data(){
			return{
				orderDetail:{},
				billList:[],
				// 底部导航
				boottomList:[
					{icon:'question-circle',title:'有疑问'},
					// #ifdef H5||APP-PLUS
					{icon:'share',title:'转发账单'},
					// #endif
				],
				headStyle:{
					color:'gray'
				},
				flag:true,
				timerID:null
			}
		},
		filters:{
			time
		},
		methods:{
			randomStr(length){
			  const chars='ABCDEFGHIJKLMNOPQRSTUVWXYZabcdefghijklmnopqrstuvwxyz0123456789';
			  let noceStr='';
			  let maxPos=chars.length;
			  while (length--) noceStr += chars[Math.random()*maxPos |0]
			  return noceStr
			},
			payment(data){
				if(this.flag){
					this.flag=false
					if(data.totalAmount=='未报价'||data.totalAmount=='0.00'){
						uni.showToast({
							title:'暂不能支付'
						})
						return;
					}
					var openId=uni.getStorageSync('openid')
					// #ifdef MP-WEIXIN
					if(!openId){
						uni.showToast({
							title:'暂不能支付'
						})
						return;
					}
					// #endif
					var fee=0.01
					var outTradeNo=this.randomStr(3)//随机数
					var subject=data.billDescription//app支付的副标题，
					if(!data.billSubject){
						uni.showToast({
							title:'订单描述报错',
							icon:'none'
						})
						return
					}
					var body=data.billSubject//订单描述
					var outTradeNo=data.billNo+outTradeNo//订单号
					var totalFee=(data.totalAmount)*100//总金额，分
					uni.showLoading({//避免接口调用时间长， 没反应
						title:'加载中'
					})
					uniCloud.callFunction({
						name: 'getOrderInfo',
						data:{
							// #ifdef MP-WEIXIN
							plateForm:'miniparamWeixin',
							// #endif
							// #ifdef APP-PLUS
							plateForm:'appWeixin',
							// #endif
							body:body,
							outTradeNo:outTradeNo,
							totalFee:totalFee,
							// #ifdef APP-PLUS
							subject:subject,//app的副标题
							// #endif
							openid:openId,
						},
						success(res) {
							uni.hideLoading()
							console.log(res)
							uni.requestPayment({
						  // #ifdef APP-PLUS
						   provider: 'wxpay', // App端此参数必填，可以通过uni.getProvider获取
						  // #endif
						  // #ifdef MP-WEIXIN
						  ...res.result.orderInfo,
						  // #endif
						  // #ifdef APP-PLUS || MP-ALIPAY
						  orderInfo: res.result.orderInfo,
						  // #endif
							...res.result.orderInfo,
								success(e){
									console.log('3')
									console.log(e)
									// errMsg: "requestPayment:ok"
									// 支付成功后,需要进行支付的禁止
									// 点击支付完成之后,如果需要跳转页面,可以在这里设置逻辑
									uni.showLoading({
										title:'加载中'
									})
									uniCloud.callFunction({//查询订单
										name: 'orderQuery',
										data:{
											outTradeNo:outTradeNo
										},
										success(e) {
											uni.hideLoading()
											console.log(e)
											if(e.result.tradeState=='SUCCESS'&&e.result.totalFee==totalFee){//支付成功，将结果返给后端，后端通过与微信发送的消息去修改订单的状态
												paymentResult({
													resultCode:e.result.resultCode,
													returnCode:e.result.returnCode,
													outTradeNo:e.result.outTradeNo,
													tradeType:e.result.tradeType,
													totalFee:e.result.totalFee,//总金额，分
													openId:e.result.openid,
													timeEnd:e.result.timeEnd,//交易完成时间
													transactionId:e.result.transactionId//微信交易号
												}).then(res=>{
													console.log(res)
													if(res.data.code==0){
														uni.navigateTo({//订单查询完之后，进行跳转页面
															url:'/pages/orderManage/orderSuccess?billNo='+data.billNo
														})
													}else{
														uni.showToast({
															title:res.data.msg,
															icon:'none'
														})
													}
												}).catch(err=>{
													console.log(err)
												})
											}
										},fail(err) {
											uni.hideLoading()
											console.log(err)//订单不存在， 返回值为ORDERNOTEXIST
										}
									})
								},
								fail(err){//取消支付
									// console.log('2')
									console.log(JSON.stringify(err))
									uni.showToast({
										title:'取消支付',
										icon:'none'
									})
									uni.report('订单支付时报错',{
										outTradeNo:outTradeNo,
										err:JSON.stringify(err)
									})
									// 小程序取消支付
									// {"errMsg":"requestPayment:fail 系统错误，错误码：1, [20201229 14:38:04][touristappid]"}
									// {"errMsg":"requestPayment:fail cancel"}
									// app取消支付
									 // {"errMsg":"requestPayment:fail [payment微信:-2]User canceled","errCode":-100,"code":-100} 
								}
							})
						},
						fail(err) {
							uni.report('获取订单支付参数时报错',{
								outTradeNo:outTradeNo,
								err:err
							})
							uni.hideLoading()
							console.log(err)
							uni.showToast({
								title:'err1'
							})
						}
					})
				}
				this.timerID = setTimeout(()=> {
					console.log('你有执行吗')
					this.flag = true
				}, 4000)
			},
			show(index,orderNo){
				if(index==0){//有疑问
					uni.navigateTo({
						url:'/pages/orderManage/orderQuestion?orderNo='+orderNo
					})
				}else{//转发账单
				console.log(orderNo)
					uni.share({
						provider: 'weixin',
						scene: "WXSceneSession",
						type: 5,
						// imageUrl: 'https://shoyii-open-1258143571.cos.ap-guangzhou.myqcloud.com/images/e805d13aa09a421cbe92e3d3b21f93ae/0a95371a0a0e4bff8f96f27f188bcf49.jpg',
						imageUrl:'/static/image/shoyiilogo.png',
						title: '账单',
						miniProgram: {
							id: 'gh_088556d792b9',
							path: 'pages/orderManage/orderDetail?orderNo='+orderNo,
							type: 0,
							webUrl: 'https://mobile.shoyii.com/#/'
						},
						success: ret => {
							console.log(JSON.stringify(ret));
						}
					});
				}
			},
			getOrderDetail(id){
				getOrderDetail({
					orderNo:id
				}).then(res=>{
					console.log(res)
					if(res.data.code==0){
						this.orderDetail=res.data.object
						this.billList=res.data.object.omsBills
					}else if(res.data.code==-1){//去登陆
						uni.navigateTo({
							url:'/pages/login/tellogin?pages='+'/pages/orderManage/orderDetail'+'&pagetype='+2,
							animationType:'pop-in'
						})
					}else{
						uni.showToast({
							title:res.data.msg,
							icon:'none'
						})
					}
				}).catch(err=>{
					console.log(err)
					uni.showToast({
						title:'err',
						icon:'none'
					})
				})
			},
			// 获取code
			getJscode(){
				uni.login({
					success:(e)=> {
						console.log(e)
						console.log(e.code)
						this.getOpenid(e.code)
					},
					fail:(err) =>{
						console.log(err)
						console.log('err')
					}
				})
			},
			getOpenid(code){
				getOpenid({
					jsCode:code
				}).then(res=>{
					console.log(res)//存储到本地
					if(res.data.code==0){
						uni.setStorageSync('openid',res.data.object);
					}else{
						uni.showToast({
							title:res.data.msg,
							icon:'none'
						})
					}
				}).catch(err=>{
					console.log(err)
				})
			}
		},
		onUnload(){
			console.log('取消了吗')
			clearTimeout(this.timerID)
		},
		onLoad(option) {
			this.getOrderDetail(option.orderNo)
			// #ifdef MP-WEIXIN
			if(uni.getStorageSync('openid')){
			}else{
				this.getJscode()//不需要登录就可以支付
			}
			// #endif
		},
		onBackPress(e){
			console.log(e)
			uni.navigateTo({
				url:'/pages/orderManage/orderManage',
				animationType:'slide-in-left'
			})
			return true
		}
	}
</script>

<style scoped lang="scss">
	.title{
		text-align: center;
		padding: 40rpx 0 0;
		color: #000000;
		font-size: 28rpx;
		.paycount{
			color: #333333;
			padding: 30rpx 0;
			font-size: 30rpx;
			.count{
				font-size: 60rpx;
				font-weight: bold;
				font-family: PingFang-SC-Bold;
				color: #000000;
				margin-right: 18rpx;
			}
		}
		
	}
	.item{
		line-height: 56rpx;
		color: #000000;
		display: flex;
	}
	.time{
		color: #8F8F8F;
		width: 112rpx;
		display: block;
	}
	.right{
		margin-right: 38rpx;
	}
	.footer{
		height:55px;
		display: flex;
		align-items: center;
		justify-content: space-between;
		background: #FFFFFF;
		position: fixed;
		bottom: 0;
		left: 0;
		right: 0;
		width: 100%;
		padding: 0 30rpx;
	}
	.vrshow{
		flex: 1;
		color: #000000;
		font-size: 24rpx;
		text-align: center;
		font-family: PingFang-SC-Medium;
		font-weight: 500;
		display: flex;
		justify-content: space-around;
	}
	.vrshow .iconfont{
		font-size: 36rpx;
	}
</style>
